<template><section><h2>Select 选择器</h2>
<h3>基本用法</h3>
<div class="demo-block mt_15 mb_15">
<tj-button plain round="3px" size="small" @click="visible = true">选择{{value}}</tj-button>
<p><tj-select title="默认" v-model="value" :visible.sync="visible" :options="options">
</tj-select></p>

</div>
<demo-block>
                    <div slot="desc"><tj-button plain round="3px" size="small" @click="visible = true">选择{{value}}</tj-button>

<tj-select title="默认" v-model="value" :visible.sync="visible" :options="options">
</tj-select>


</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">plain</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"visible = true"</span>&gt;</span>选择{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">tj-select</span> 
  <span class="hljs-attr">title</span>=<span class="hljs-string">"默认"</span> 
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>
  <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"visible"</span> 
  <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tj-select</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">visible</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">value</span>: <span class="hljs-string">"选项3"</span>,
      <span class="hljs-attr">options</span>: [<span class="hljs-string">"选项1"</span>, <span class="hljs-string">"选项2"</span>, <span class="hljs-string">"选项3"</span>, <span class="hljs-string">"选项4"</span>, <span class="hljs-string">"选项5"</span>],
      <span class="hljs-attr">visible2</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">value2</span>: <span class="hljs-number">2</span>,
      <span class="hljs-attr">options2</span>: [],
      <span class="hljs-attr">visible3</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">value3</span>: [],
      <span class="hljs-attr">options3</span>: []
    }
  },
  mounted() {
    <span class="hljs-keyword">for</span>(<span class="hljs-keyword">var</span> i = <span class="hljs-number">0</span>; i &lt; <span class="hljs-number">5</span>; i++) {
      <span class="hljs-keyword">this</span>.options2.push({
        <span class="hljs-attr">id</span>: i,
        <span class="hljs-attr">name</span>: <span class="hljs-string">"选项"</span> + i
      })
      <span class="hljs-keyword">this</span>.value3.push({
        <span class="hljs-attr">id</span>: i,
        <span class="hljs-attr">name</span>: <span class="hljs-string">"选项"</span> + i
      })

      <span class="hljs-keyword">var</span> children = []

      <span class="hljs-keyword">for</span>(<span class="hljs-keyword">var</span> j = <span class="hljs-number">0</span>; j &lt; <span class="hljs-number">5</span>; j++) {

        <span class="hljs-keyword">var</span> childrens = []
        <span class="hljs-keyword">for</span>(<span class="hljs-keyword">var</span> k = <span class="hljs-number">0</span>; k &lt; <span class="hljs-number">5</span>; k++) {
          childrens.push({
            <span class="hljs-attr">id</span>: k,
            <span class="hljs-attr">name</span>: <span class="hljs-string">"第三列"</span> + k
          })
        }

        children.push({
          <span class="hljs-attr">id</span>: j,
          <span class="hljs-attr">name</span>: <span class="hljs-string">"第二列"</span> + j,
          <span class="hljs-attr">children</span>: childrens
        })
      }

      <span class="hljs-keyword">this</span>.options3.push({
        <span class="hljs-attr">id</span>: i,
        <span class="hljs-attr">name</span>: <span class="hljs-string">"第一列"</span> + i,
        <span class="hljs-attr">children</span>: children
      })
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>选项对象</h3>
<p>value-key 为对象中文字对应的 key。</p>
<p>select-key 为 v-model 中展示的 key，为空默认整个对象。</p>
<div class="demo-block mt_15 mb_15">
<tj-button plain round="3px" size="small" @click="visible2 = true">选择{{value2}}</tj-button>
<tj-select title="选项对象" v-model="value2" :visible.sync="visible2" value-key="name" select-key="id" :options="options2">
</tj-select>
</div>
<demo-block>
                    <div slot="desc"><tj-select title="选项对象" v-model="value2" :visible.sync="visible2" value-key="name" select-key="id" :options="options2">
</tj-select>
</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-select</span> 
  <span class="hljs-attr">title</span>=<span class="hljs-string">"选项对象"</span> 
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span>
  <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"visible2"</span> 
  <span class="hljs-attr">value-key</span>=<span class="hljs-string">"name"</span>
  <span class="hljs-attr">select-key</span>=<span class="hljs-string">"id"</span>
  <span class="hljs-attr">:options</span>=<span class="hljs-string">"options2"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tj-select</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>多列联动</h3>
<p>对象子集列表 key 需为 children。</p>
<div class="demo-block mt_15 mb_15">
<tj-button plain round="3px" size="small" @click="visible3 = true">{{value3[0].name}}{{value3[1].name}}{{value3[2].name}}</tj-button>
<tj-select title="多列联动" v-model="value3" :visible.sync="visible3" value-key="name" :item-count="3" :options="options3">
</tj-select>
</div>
<demo-block>
                    <div slot="desc"><tj-select title="多列联动" v-model="value3" :visible.sync="visible3" value-key="name" :item-count="3" :options="options3">
</tj-select>
</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-select</span> 
  <span class="hljs-attr">title</span>=<span class="hljs-string">"多列联动"</span> 
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span>
  <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"visible3"</span> 
  <span class="hljs-attr">value-key</span>=<span class="hljs-string">"name"</span>
  <span class="hljs-attr">:item-count</span>=<span class="hljs-string">"3"</span>
  <span class="hljs-attr">:options</span>=<span class="hljs-string">"options3"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tj-select</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Props</h3>
<div class="demo-block table-wrap">
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>v-model</td>
<td>选中值</td>
<td></td>
<td></td>
<td></td>
</tr>
<tr>
<td>visible</td>
<td>显示状态</td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>options</td>
<td>列表数组</td>
<td>array</td>
<td></td>
<td></td>
</tr>
<tr>
<td>valueKey</td>
<td>对象中文字对应的key</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>selectKey</td>
<td>选中对象显示对应的key</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>itemCount</td>
<td>显示的列数</td>
<td>number</td>
<td></td>
<td>1</td>
</tr>
</tbody>
</table>
</div>
<h3>Events</h3>
<div class="demo-block table-wrap">
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>confirm</td>
<td>点击确认触发</td>
<td></td>
</tr>
<tr>
<td>cancel</td>
<td>点击取消触发</td>
<td></td>
</tr>
<tr>
<td>change</td>
<td>滑动选项时触发</td>
<td>picker, values</td>
</tr>
</tbody>
</table>
</div></section></template>

<script>
export default {
  data() {
    return {
      visible: false,
      value: "选项3",
      options: ["选项1", "选项2", "选项3", "选项4", "选项5"],
      visible2: false,
      value2: 2,
      options2: [],
      visible3: false,
      value3: [],
      options3: []
    }
  },
  mounted() {
    for(var i = 0; i < 5; i++) {
      this.options2.push({
        id: i,
        name: "选项" + i
      })
      this.value3.push({
        id: i,
        name: "选项" + i
      })

      var children = []

      for(var j = 0; j < 5; j++) {

        var childrens = []
        for(var k = 0; k < 5; k++) {
          childrens.push({
            id: k,
            name: "第三列" + k
          })
        }

        children.push({
          id: j,
          name: "第二列" + j,
          children: childrens
        })
      }

      this.options3.push({
        id: i,
        name: "第一列" + i,
        children: children
      })
    }
  }
}
</script>


